<template>
        <div class="card">
            <div v-show="!loader">
                <img  @click="abrirModal" class="image" :src="cover" alt="">
                <div class="card-content">
                    <span class="icon">
                    <i class="far fa-star"></i>
                    </span>
                    <button @click="abrirModal" class="boton-modal"> Ver Foto </button>
                </div>
            </div>
        </div>
</template>

<script>
export default {

    props: {
        id: {
            type: Number,
        },
        cover: {
            type: String,
        },

        img: {
            type: String,
        }

    },

    data(){
        return {
            loader: false
        }
    },

    methods:{
        abrirModal(){
            let data = {
                id: this.id,
                img: this.img
            }
            this.$emit('modalOn', data)
        },
    }

}
</script>

<style >
.image{
    max-width: 100%;
    display: block;
    object-fit: cover;
    width: 20vw;
    height: 50vh;
    border-radius: 5px 5px 0 0;
}

.boton-modal{
    border: none;
    color: #fff;
    border-radius: 20px;
    font-size: 1rem;
    padding: .4rem 1rem;
    background: rgb(223, 11, 11);
    cursor: pointer;
}

.card{
    width: 20vw;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    border-radius: 5px;
    transition: transform .6s;
    cursor: pointer;
}

.card:hover{
    transition: .3s;
    transform: scale(1.05);
    box-shadow: 1px 0px 16px -6px rgba(0,0,0,0.64);
}

.card-content{
    display: flex;
    align-items: center;
    justify-content: space-around;
    padding: 1.5rem 1.8rem;
}


.icon{
    font-size: 1.6rem;
    cursor: pointer;
}

</style>
