import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    photos: [],
    loader: false,
    page: 1,
    pages: [],
    search: ''
  },
  mutations: {

    //agregando photos al array 
    agregarPhotos(state,payload){
      state.photos = []
      state.pages = []


      state.pages.push(Math.round(payload.totalHits / 8))
      state.loader = true
      
      
      state.photos = payload.hits

      setTimeout( () =>{
        state.loader = false
      }, 3200 )

    },

  },
  actions: {

    //busqueda de fotos
    buscarPhotos: async function({commit, state}, q){
      state.page = 1
      if(q != '') {
        state.search = q


        console.log(state.search)
        
        let data = await fetch(`https://pixabay.com/api/?key=15429057-599417777224064574b971e1f&q=${q}&lang=es&image_type=photo&per_page=8&page=1`)
        data = await data.json()
        commit('agregarPhotos', data)
      }

      if(q == ''){
        state.search = 'populares'

        console.log('buscando',state.search)
        let data = await fetch(`https://pixabay.com/api/?key=15429057-599417777224064574b971e1f&lang=es&image_type=photo&per_page=8&page=1`)
        data = await data.json()
        commit('agregarPhotos', data)
      }
      
    },


    //cambiando a la siguiente pagina
    nextPage: async function({commit, state}){
      state.loader = true

      //pagina de populares
      if(state.search === 'populares'){
        state.page +=1
        let pagina = state.page 
        if( pagina <= state.pages ){

          let data = await fetch(`https://pixabay.com/api/?key=15429057-599417777224064574b971e1f&lang=es&image_type=photo&per_page=8&page=${pagina}`)
          data = await data.json()
          commit('agregarPhotos', data)
        }
      }

      //pagina de busqueda especifica
      if(state.search != 'populares'){
        let q = state.search
        state.page +=1
        let pagina = state.page 
        if( pagina <= state.pages ){

          let data = await fetch(`https://pixabay.com/api/?key=15429057-599417777224064574b971e1f&q=${q}&lang=es&image_type=photo&per_page=8&page=${pagina}`)
          data = await data.json()
          commit('agregarPhotos', data)
        }
      }
    },


    backPage: async function({commit, state}){
      state.loader = true
      if(state.page > 1){
        if(state.search === 'populares'){

          state.page -=1
          let pagina = state.page 


          let data = await fetch(`https://pixabay.com/api/?key=15429057-599417777224064574b971e1f&lang=es&image_type=photo&per_page=8&page=${pagina}`)
          data = await data.json()
          commit('agregarPhotos', data)
        }

        if(state.search != 'populares'){
          let q = state.search
          state.page -=1
          let pagina = state.page 
  
          let data = await fetch(`https://pixabay.com/api/?key=15429057-599417777224064574b971e1f&q=${q}&lang=es&image_type=photo&per_page=8&page=${pagina}`)
          data = await data.json()
          commit('agregarPhotos', data)
        }
      }
      
    }
  },
  modules: {
  }
})
